import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_MERGE_CELLS = Symbol('TABLE_MERGE_CELLS');

export function mergeTableCells(table) {
  return {
    type: TABLE_MERGE_CELLS,
    payload: {
      node: table,
    },
  };
}

interface TableMergeCellsPayload {
  node: Table;
}

interface TableMergeCellsAction {
  type: typeof TABLE_MERGE_CELLS;
  payload: TableMergeCellsPayload;
}

export function isTableMergeCellsAction(
  action: Action,
): action is TableMergeCellsAction {
  return action.type === TABLE_MERGE_CELLS;
}
